import React from 'react';
import '../styles/QuestList.css';
import QuestItem from './QuestItem';

function QuestList() {
  const quests = [
    { id: 1, name: '    ', reward: 200 },
    { id: 2, name: ' ', reward: 100 },
    { id: 3, name: ' 5 ', reward: 800 },
    { id: 4, name: ' ', reward: 150 },
    { id: 5, name: '          BallCry', reward: 300 },
    { id: 6, name: '    X', reward: 100 },
    { id: 7, name: '  Ton', reward: 250 },
  ];

  return (
    <ul className="quest-list">
      {quests.map(quest => (
        <QuestItem key={quest.id} quest={quest} />
      ))}
    </ul>
  );
}

export default QuestList;